$variable-prefix:             mvo-;

$enable-shadows:    true;
$enable-gradients:    true;

$body-text-align:    center;

$body-bg:    $light;

$link-color:    $primary;

$component-active-color:    $light;

$input-bg:    $light;

$badge-color:    $light;

$modal-content-border-color:    $dark_0-2;
$modal-backdrop-bg:    $dark;

$list-group-border-color:    $dark_0-125;

$box-shadow:    0 .5rem 1rem $dark_0-15;
$box-shadow-sm:    0 .125rem .25rem $dark_0-075;
$box-shadow-lg:    0 1rem 3rem $dark_0-175;
$box-shadow-inset:    inset 0 1px 2px $dark_0-075;

$component-active-color:      $light;

$btn-active-box-shadow:    inset 0 3px 5px $dark_0-125;
$btn-box-shadow:    inset 0 1px 0 $light_0-15, 0 1px 1px $dark_0-075;

$form-check-input-border:    1px solid $dark_0-25;

$form-switch-color:    $dark_0-25;

$form-range-thumb-box-shadow:    0 .1rem .25rem $dark_0-1;

$navbar-dark-color:    $light_0-55;
$navbar-dark-hover-color:    $light_0-75;
$navbar-dark-active-color:    $light;
$navbar-dark-disabled-color:    $light_0-25;
$navbar-dark-toggler-border-color:    $light_0-1;

$navbar-light-color:    $dark_0-55;
$navbar-light-hover-color:    $dark_0-7;
$navbar-light-active-color:    $dark_0-9;
$navbar-light-disabled-color:    $dark_0-3;
$navbar-light-toggler-border-color:    $dark_0-1;

$dropdown-border-color:    $dark_0-15;
$dropdown-dark-link-hover-bg:           $light_0-15;

$card-border-color:    $dark_0-125;
$card-cap_bg:    $dark_0-03;

$accordion-border-color:               $dark_0-125;

$tooltip-color:                             $light;
$tooltip-bg:                        $dark;

$popover-bg:                        $light;
$popover-border-color:              $dark_0-2;

$toast-background-color:    $light_0-85;
$toast-border-color:    $dark_0-1;
$toast-header-background-color:    $light_0-85;
$toast-header-border-color:    $dark_0-05;

$badge-color:     $light;

$modal-content-bg:    $light;
$modal-content-border-color:    $dark_0-2;
$modal-backdrop-bg:    $dark;

$progress-bar-color:    $light;

$list-group-bg:    $light;
$list-group-border-color:    $dark_0-125;

$carousel-control-color:    $light;
$carousel-indicator-active-bg:    $light;
$carousel-caption-color:    $light;
$carousel-dark-indicator-active-bg:    $dark;
$carousel-dark-caption-color:    $dark;

$btn-close-color:    $dark;

$kbd-color:    $light;
